body {
  font-family: "STIX Two Text", serif;
  font-size: 1.6rem;
  line-height: 1.8; }

p {
  color: #000000;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

.contenedor-dos {
  width: 90%;
  max-width: 70rem;
  margin: 0 auto; }

a {
  text-decoration: none; }

img,
picture {
  width: 100%;
  display: block; }

h1,
h2,
h3 {
  font-family: "Lobster", cursive;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

.boton-verde {
  display: inline-block;
  background-color: #6A994E;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none; }
  .boton-verde:hover {
    cursor: pointer;
    background-color: #5e8845; }

.boton-verde-block {
  display: block;
  background-color: #6A994E;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  width: 100%; }
  .boton-verde-block:hover {
    cursor: pointer;
    background-color: #5e8845; }

.footer {
  margin-top: 2.5rem;
  padding: 1rem 0;
  text-align: center;
  background-color: #386641; }
  .footer p {
    color: #FFFFFF;
    margin: 0; }

.formulario-login {
  font-size: 2rem; }
  .formulario-login legend {
    font-size: 2.8rem;
    font-family: "Lobster", cursive; }
  .formulario-login input:not([type="submit"]) {
    display: block;
    width: 100%;
    padding: .3rem 1rem;
    margin-bottom: 2rem; }

/* PAGINAS LIBRES */
.header {
  background-color: #386641; }
  .header .navbar {
    display: flex;
    justify-content: space-around;
    padding: 2rem 0;
    color: #FFFFFF; }
    .header .navbar .logo {
      font-family: "Lobster", cursive;
      text-transform: uppercase;
      font-size: 4rem; }
    .header .navbar a {
      color: #FFFFFF; }

.ingreso-registro {
  display: flex;
  justify-content: flex-end;
  padding-top: 1rem;
  font-size: 2rem; }
  .ingreso-registro .navegacion-enlace {
    color: #FFFFFF; }
    .ingreso-registro .navegacion-enlace:last-of-type {
      padding-left: 2rem; }
    .ingreso-registro .navegacion-enlace:first-of-type {
      padding-right: 2rem; }
    .ingreso-registro .navegacion-enlace:hover {
      color: #000000; }
  .ingreso-registro div {
    color: #FFFFFF; }

.navegacion {
  font-size: 2.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .navegacion .navegacion-enlace {
    margin-right: 3rem; }
    .navegacion .navegacion-enlace.last-of-type {
      margin-right: 0; }
    .navegacion .navegacion-enlace:hover {
      color: #000000; }

.nosotros {
  display: grid;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 2rem; }
  .nosotros .nosotros-div {
    text-align: center; }

.imagen-contacto {
  background-image: url("../img/fondo4.jpg");
  background-position: right bottom;
  background-size: cover;
  padding: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 5rem; }
  .imagen-contacto h2 {
    font-size: 4rem;
    color: #FFFFFF;
    font-weight: 700; }
  .imagen-contacto p {
    font-size: 3rem;
    color: #FFFFFF; }

.inicio {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(2, 1fr); }

.video-inicio video {
  max-width: 60rem;
  max-height: 80rem;
  border-radius: 2rem; }
